import React from "react";
import Card from "../Card/Card";
import importAllImages from '../Helpers/ImportAllImages';
import localsCardData from './LocalsData.json';

const localsImages = importAllImages(require.context('../PartnersLogos/Locals', false, /\.(svg)$/));

const Locals = () => {
    return (
        <>
            <h2>Locals</h2>
            <div className="section-container">
                {localsImages.map((localImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={localImage}
                        partner_name={localsCardData.partner_name[index]}
                    />
                ))}
            </div>
        </>
    );
}

export default Locals;